ul {
    list-style-type: none;
	margin: 0;
    padding: 0;
}
.img {
	background-size: cover;
    background-position: center;
	margin: 2.5%;
	width: 95%;
	height: 50vh;
	display: table;
	position: relative;
    border-radius: 15px;
    box-shadow: 1px 1px 20px -5px;  
	transition-duration: 0.2s;
    transition-timing-function: linear;
}
.showinbasket {
    width: 47.5%;
    height: 25vh;
	transition-duration: 0.2s;
    transition-timing-function: linear;
}
.img_title {
    font-size: calc(16px + 2vmin);
    color: black;
    font-weight: bold;
	display: flex;
    align-items: center;
    justify-content: space-between;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0;
    padding: 15px;
    position: absolute;
    border-radius: 15px;
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    background-color: rgba(255, 255, 255, 0.5);
    box-shadow: 0px 5px 10px -5px;
	transition-duration: 0.2s;
    transition-timing-function: linear;
}
.showinbasket .img_title {
    width: 100%;
    height: 25vh;
    position: absolute;
    margin-left: 100%;
    box-shadow: none;
    background: none;
    -webkit-backdrop-filter: none;
    backdrop-filter: none;
    flex-wrap: wrap;
	transition-duration: 0.2s;
    transition-timing-function: linear;
}
.countbox {
	display: flex;
	align-items: center;
}
.countbox_buttonminus {
    font-size: calc(16px + 2vmin);
    color: black;
    font-weight: bold;
    margin: 0px;
    padding: 0px;
	width: 47px;
    height: 47px;
    border-radius: 100%;
    border: 0px solid yellow;
    background: yellow;
    box-shadow: 1px 1px 20px -5px;
}
.countbox_input {
	border: none;
    background: none;
    width: 60px;
    font-size: calc(16px + 2vmin);
    color: black;
    font-weight: bold;
	text-align: center;
	}
.countbox_input[value="0"] , .countbox_input[value=""] , .countbox_buttonminus:disabled {
    opacity: 0;
	}
.countbox_buttonplus {
    font-size: calc(16px + 2vmin);
    color: black;
    font-weight: bold;
	margin: -10px;
    margin-top: -45px;
    padding: 0px;
    width: 90px;
    height: 90px;
    border-radius: 100%;
    border: 0px solid yellow;
    background: yellow;
    box-shadow: 1px 1px 20px -5px;
	transition-duration: 0.2s;
    transition-timing-function: linear;
}
.showinbasket .countbox_buttonplus {
	width: 47px;
    height: 47px;
    margin: 0px;
    padding: 0px;
	transition-duration: 0.2s;
    transition-timing-function: linear;
	}
button:hover, .popup_inner button:hover {
    box-shadow: 1px 1px 10px 0px;
    background: #ffcf00;
}
.countbox_buttonplus:disabled {
    background: #b5b5b5;
    box-shadow: 1px 1px 10px -5px;
}
button:focus, input:focus {
    outline: none;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
	margin: 0;
}
input[type='number'] {
    -moz-appearance: textfield;
}



.footer {
  position: sticky;
  bottom: 0px;
  width: 100%;
  margin: 0px;
  height: auto;
  z-index: 50;
    background: #fff;
	display: -webkit-flex;
    display: flex;
    flex-direction: column;
}
.footer p {	margin: 10px 2.5%;
    padding: 5px;}
.footer_button {
    font-size: calc(16px + 2vmin);
    color: black;
    font-weight: bold;
	text-align: center;
	margin: 10px 2.5%;
    padding: 5px;
	flex: 1 1 60px;
    border: 0px solid yellow;
    background: yellow;
	cursor: pointer; 
}
.hide, .hideinbasket {
	display: none;
	transition-duration: 0.2s;
    transition-timing-function: linear;
}




* {box-sizing: border-box;
  font-size: calc(16px + 2vmin);}
.top {
    margin: 0 auto;
    width: 95%;
    position: sticky;
    top: 10px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    z-index: 50;
}
.search {
  position: relative;
  width: 300px;
  margin: 10px;
  height: 47px;
  z-index: 50;
}
.search input {
    height: 47px;
    width: 0;
    padding: 0 32px 0 15px;
    border: none;
    outline: none;
    background: #ffff0078;
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    transition: .4s cubic-bezier(0, 0.8, 0, 1);
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    border-radius: 50px;
    box-shadow: 1px 1px 20px -5px;
	opacity: 0;
	font-size: calc(16px + 2vmin);
    color: black;
    font-weight: bold;
}
.search input:focus, .search input.notempty {
	width: 300px;
	z-index: 1;
	opacity: 1;
}
.search button, .popup_inner button {
	background: yellow;
    border: none;
    height: 47px;
    width: 47px;
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
    border-radius: 50px;
    box-shadow: 1px 1px 20px -5px;
}
.search button.notempty {
z-index: 2;
}
.search button:before{
    content: "\f002";
    font-family: FontAwesome;
	font-size: calc(16px + 2vmin);
    color: black;
}
.search button.notempty:before, .popup_inner button:before{
    content: "\f00d";
    font-family: FontAwesome;
	font-size: calc(16px + 2vmin);
    color: red;
}
.basket, .user {
	background: yellow;
    border: none;
    height: 47px;
    width: 47px;
    top: 0;
    right: 0;
    cursor: pointer;
    border-radius: 50px;
    box-shadow: 1px 1px 20px -5px;
}
.basket:before{
    content: "\f07a";
    font-family: FontAwesome;
	font-size: calc(16px + 2vmin);
    color: black;
}
.basket.back:before{
    content: "\f053  ";
    font-family: FontAwesome;
	font-size: calc(16px + 2vmin);
    color: black;
}
.user:before{
    content: "\f007";/*007 234+  00cv*/
    font-family: FontAwesome;
	font-size: calc(16px + 2vmin);
    color: black;
}
.popup {
	position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 60;
    display: flex;
    align-items: center;
    justify-content: center;    
	-webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    background-color: rgba(255, 255, 255, 0.5);
}
.popup_inner {font-size: calc(16px + 2vmin);
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: center;
    background: white;
    padding: 15px;
    box-shadow: 0 0 15px -5px black;
}
.popup_inner form {    
	position: relative;
	display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: center;
}
.popup_inner form label {
	display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.popup_inner button {
	position: absolute;
    right: 0;
    top: 0;
    margin: -20px;
}


input {
	outline: none;
	border: none;
}

textarea {
  outline: none;
  border: none;
}

textarea:focus, input:focus {
  border-color: transparent !important;
}
.wrap-input100 {
  width: 100%;
  position: relative;
  border-bottom: 2px solid #d9d9d9;
  margin: 7px 0;
}

.label-input100 {
  font-size: calc(16px + 2vmin);
  color: #333333;
  line-height: 1.5;
  padding-left: 7px;
}

.input100 {
  font-size: calc(16px + 2vmin);
  color: #333333;
  line-height: 1.2;

  display: block;
  width: 100%;
  height: 55px;
  background: transparent;
  padding: 0 7px 0 55px;
}


/*---------------------------------------------*/
.focus-input100 {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
}

.focus-input100.userinput::after {
content: '\f007';
}
.focus-input100.phoneinput::after {
content: '\f095';
}
.focus-input100::after {
	font-family: FontAwesome;
    color: #adadad;
    font-size: calc(16px + 2vmin);
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    /* height: calc(100%); */
    bottom: 0;
    left: 0;
    top: 0;
    padding-left: 13px;
    /* padding-top: 3px; */
    color: yellow;
    text-shadow: 0 0 2px black;
}

.focus-input100::before {
  content: "";
  display: block;
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 2px;
  background: #7f7f7f;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  -moz-transition: all 0.4s;
  transition: all 0.4s;
}


.input100:focus + .focus-input100::before {
  width: 100%;
}

.has-val.input100 + .focus-input100::before {
  width: 100%;
}

.input100:focus + .focus-input100::after {
color: #ffcf00;  
}

.has-val.input100 + .focus-input100::after {
  color: yellow;
  text-shadow: 0 0 1px black;
}

button:hover {
    cursor: pointer;
}